<template>
    <v-container class="grey lighten-5">
        <v-card>
            <v-toolbar color="blue lighten-1" dark>
                <v-toolbar-title class="flex text-xs-center"
                    >QCC</v-toolbar-title
                >
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                ></v-text-field>
            </v-toolbar>

            <v-toolbar flat color="white">
                <v-toolbar-title class="flex text-xs-center"
                    >Document Detail</v-toolbar-title
                >
            </v-toolbar>
            <div
                class="d-inline pa-2 grey lighten-2 ml-4  accent-4 black--text"
            >
                MACBOOK-001
            </div>
            <div class="d-inline pa-2 blue white--text">Macbook</div>
            <div class="d-inline pa-2 deep-purple accent-4 white--text">
                Apple
            </div>
            <div class="d-inline pa-2 blue white--text">USA</div>

            <v-data-table
                :headers="headers"
                :items="documentDetail"
                class="elevation-1"
                :search="search"
            >
                <template v-slot:top>
                    <v-toolbar flat color="white">
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="500px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    color="primary"
                                    dark
                                    class="mb-2"
                                    v-bind="attrs"
                                    v-on="on"
                                    >New Item</v-btn
                                >
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">{{
                                        formTitle
                                    }}</span>
                                </v-card-title>
                                <v-card-text>
                                    <v-container>
                                        <v-row>
                                            <v-col cols="12">
                                                <v-text-field
                                                    v-model="
                                                        editedItem.attachName
                                                    "
                                                    label="Attach name"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col cols="12">
                                                <v-file-input
                                                    v-model="
                                                        editedItem.attachFile
                                                    "
                                                    accept=".pdf"
                                                    label="Attach file"
                                                    placeholder="Select your files"
                                                    prepend-icon="mdi-paperclip"
                                                    outlined
                                                    multiple
                                                ></v-file-input>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="close"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="save"
                                        >Save</v-btn
                                    >
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>
                <template v-slot:item.attachFile>
                    <v-btn
                        text
                        icon
                        color="blue"
                        :href="urlFileAttact"
                        target="_blank"
                    >
                        <v-icon>mdi-file-pdf-outline</v-icon>
                    </v-btn>
                </template>
                <template v-slot:item.actions="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                    </v-icon>
                    <v-icon small @click="deleteItem(item)">
                        mdi-delete
                    </v-icon>
                </template>
            </v-data-table>
        </v-card>
    </v-container>
</template>

<script>
import APIConfig from '../config/APIConfig'
export default {
    name: 'DocumentDetail',
    data: () => ({
        dialog: false,
        search: '',
        documentDetail: [],
        editedIndex: -1,
        editedItem: {
            attachName: '',
            attachFile: []
        },
        defaultItem: {
            attachName: '',
            attachFile: []
        }
    }),
    computed: {
        urlFileAttact() {
            return APIConfig.downloads + '1589526259853.pdf'
        },
        formTitle() {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
        },
        headers() {
            return [
                {
                    text: 'Attach name',
                    align: 'center',
                    value: 'attachName'
                },
                {
                    text: 'Attach file',
                    align: 'center',
                    value: 'attachFile',
                    sortable: false
                },
                { text: 'Actions', value: 'actions', sortable: false }
            ]
        }
    },

    watch: {
        dialog(val) {
            val || this.close()
        }
    },

    created() {
        this.initialize()
    },

    methods: {
        initialize() {
            this.documentDetail = [
                {
                    attachName: 'A-01',
                    attachFile: []
                },
                {
                    attachName: 'A-02',
                    attachFile: []
                }
            ]
        },

        editItem(item) {
            this.editedIndex = this.documentDetail.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        deleteItem(item) {
            const index = this.documentDetail.indexOf(item)
            confirm('Are you sure you want to delete this item?') &&
                this.documentDetail.splice(index, 1)
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(
                    this.documentDetail[this.editedIndex],
                    this.editedItem
                )
            } else {
                this.documentDetail.push(this.editedItem)
            }
            this.close()
        }
    }
}
</script>

<style lang="scss" scoped></style>
