<template>
    <v-container class="grey lighten-5">
        <v-card>
            <v-toolbar color="blue lighten-1" dark>
                <v-toolbar-title class="flex text-xs-center"
                    >QCC</v-toolbar-title
                >
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                ></v-text-field>
            </v-toolbar>
            <v-data-table
                :headers="headers"
                :items="documents"
                class="elevation-1"
                :search="search"
            >
                <template v-slot:top>
                    <v-toolbar flat color="white">
                        <v-toolbar-title>Document Lists</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="500px">
                            <template v-slot:activator="{ on, attrs }">
                                <v-btn
                                    color="primary"
                                    dark
                                    class="mb-2"
                                    v-bind="attrs"
                                    v-on="on"
                                    >New Item</v-btn
                                >
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">{{
                                        formTitle
                                    }}</span>
                                </v-card-title>

                                <v-card-text>
                                    <v-container>
                                        <v-row>
                                            <v-col cols="12">
                                                <v-select
                                                    v-model="
                                                        editedItem.countryName
                                                    "
                                                    :items="countryList"
                                                    label="Country"
                                                ></v-select>
                                            </v-col>
                                            <v-col cols="12">
                                                <v-select
                                                    v-model="
                                                        editedItem.customerName
                                                    "
                                                    :items="customerList"
                                                    label="Customer"
                                                ></v-select>
                                            </v-col>
                                            <v-col cols="12">
                                                <v-select
                                                    v-model="
                                                        editedItem.brandName
                                                    "
                                                    :items="brandList"
                                                    label="Brand"
                                                ></v-select>
                                            </v-col>
                                            <v-col cols="12">
                                                <v-text-field
                                                    v-model="
                                                        editedItem.documentName
                                                    "
                                                    label="Document"
                                                ></v-text-field>
                                            </v-col>
                                            <v-col cols="12">
                                                <v-file-input
                                                    v-model="
                                                        editedItem.documentReview
                                                    "
                                                    accept=".pdf"
                                                    label="Document Review"
                                                    placeholder="Select your files"
                                                    prepend-icon="mdi-paperclip"
                                                    multiple
                                                ></v-file-input>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="close"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="save"
                                        >Save</v-btn
                                    >
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>
                <template v-slot:item.qrcode="{ item }">
                    <qrcode
                        :value="
                            `http://192.168.1.69:8080/documents?search=${item.documentName}`
                        "
                        :options="{ scale: 1 }"
                    ></qrcode>
                </template>
                <template v-slot:item.documentReview>
                    <v-btn
                        text
                        icon
                        color="blue"
                        :href="urlFileAttact"
                        target="_blank"
                    >
                        <v-icon>mdi-file-pdf-outline</v-icon>
                    </v-btn>
                </template>
                <template v-slot:item.actions="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                    </v-icon>
                    <v-icon small @click="deleteItem(item)">
                        mdi-delete
                    </v-icon>
                </template>
                <template v-slot:item.detail>
                    <v-btn text icon color="indigo" to="/document/1">
                        <h5>Detail</h5>
                    </v-btn>
                </template>
            </v-data-table>
        </v-card>
    </v-container>
</template>

<script>
import VueQrcode from '@chenfengyuan/vue-qrcode'
import APIConfig from '../config/APIConfig'
// import InitialConfig from '../config/InitialConfig';
export default {
    name: 'Documents',
    data: () => ({
        dialog: false,
        search: '',
        documents: [],
        countryList: ['USA'],
        customerList: ['Apple'],
        brandList: ['Macbook', 'Iphone', 'Ipad'],
        editedIndex: -1,
        editedItem: {
            documentName: '',
            documentReview: [],
            brandName: '',
            customerName: '',
            countryName: ''
        },
        defaultItem: {
            documentName: '',
            documentReview: [],
            brandName: '',
            customerName: '',
            countryName: ''
        }
    }),
    computed: {
        urlFileAttact() {
            return APIConfig.downloads + '1589526259853.pdf'
        },
        formTitle() {
            return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
        },
        headers() {
            return [
                {
                    text: 'QRCode',
                    value: 'qrcode',
                    align: 'center',
                    sortable: false
                },
                {
                    text: 'Document',
                    align: 'center',
                    value: 'documentName'
                },
                {
                    text: 'Review',
                    align: 'center',
                    value: 'documentReview',
                    sortable: false
                },
                {
                    text: 'Brand',
                    align: 'center',
                    value: 'brandName'
                },
                {
                    text: 'Customer',
                    align: 'center',
                    value: 'customerName'
                },
                {
                    text: 'Country',
                    align: 'center',
                    value: 'countryName'
                },
                { text: 'Actions', value: 'actions', sortable: false },
                {
                    text: '',
                    value: 'detail',
                    align: 'center',
                    sortable: false
                }
            ]
        }
    },

    components: {
        qrcode: VueQrcode
    },

    watch: {
        dialog(val) {
            val || this.close()
        }
    },

    created() {
        this.initialize()
    },

    methods: {
        initialize() {
            this.search = this.$route.query.search || ''
            this.documents = [
                {
                    documentName: 'MACBOOK-001',
                    documentReview: [],
                    brandName: 'Macbook',
                    customerName: 'Apple',
                    countryName: 'USA'
                },
                {
                    documentName: 'IPHONE-001',
                    documentReview: [],
                    brandName: 'Iphone',
                    customerName: 'Apple',
                    countryName: 'USA'
                },
                {
                    documentName: 'IPAD-001',
                    documentReview: [],
                    brandName: 'Ipad',
                    customerName: 'Apple',
                    countryName: 'USA'
                }
            ]
        },

        editItem(item) {
            this.editedIndex = this.documents.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        deleteItem(item) {
            const index = this.documents.indexOf(item)
            confirm('Are you sure you want to delete this item?') &&
                this.documents.splice(index, 1)
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },

        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.documents[this.editedIndex], this.editedItem)
            } else {
                this.documents.push(this.editedItem)
            }
            this.close()
        }
    }
}
</script>

<style lang="scss" scoped></style>
