import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import ManageCustomers from '../views/Manages/ManageCustomers'
import ManageCountries from '../views/Manages/ManageCountries'
import ManageBrands from '../views/Manages/ManageBrands'
import DocumentDetail from '../views/DocumentDetail'
import ManageUsers from '../views/Manages/ManageUsers'
import Documents from '../views/Documents'
import Login from '../views/Auth/Login'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Documents
    },
    {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/documents',
        name: 'Documents',
        component: Documents
    },
    {
        path: '/document/:id',
        name: 'DocumentDetail',
        component: DocumentDetail
    },

    //Manage
    {
        path: '/manage/countries',
        name: 'ManageCountries',
        component: ManageCountries
    },
    {
        path: '/manage/customers',
        name: 'ManageCustomers',
        component: ManageCustomers
    },
    {
        path: '/manage/brands',
        name: 'ManageBrands',
        component: ManageBrands
    },
    {
        path: '/manage/users',
        name: 'ManageUsers',
        component: ManageUsers
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
