import Vue from 'vue';
import Vuex from 'vuex';
import APIConfig from '../config/APIConfig';
import axios from 'axios';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('x-access_token') || null,
    username: JSON.parse(localStorage.getItem('username')) || null
  },
  mutations: {
    changeToken(state, token) {
      state.token = token;
    },
    changeUsername(state, username) {
      state.username = username;
    }
  },
  actions: {
    retrieveToken(context, credentials) {
      return new Promise((resolve, reject) => {
        try {
          const getDataSignin = axios.post(APIConfig.auth + 'signin', {
            username: credentials.username,
            password: credentials.password
          });

          const { token, username } = getDataSignin.data;
          localStorage.setItem('x-access_token', token);
          localStorage.setItem('username', JSON.stringify(username));
          context.commit('changeToken', token);
          context.commit('changeUsername', username);

          resolve({ success: true });
        } catch (err) {
          reject(alert('Wrong Password!!'));
        }
      });
    }
  },
  modules: {}
});
