<template>
    <v-container class="grey lighten-5">
        <v-card>
            <v-toolbar color="blue lighten-1" dark>
                <v-toolbar-title class="flex text-xs-center"
                    >QCC</v-toolbar-title
                >
                <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
                ></v-text-field>
            </v-toolbar>
            <v-data-table :headers="headers" :items="users" class="elevation-1">
                <template v-slot:top>
                    <v-toolbar flat color="white">
                        <v-toolbar-title>Users</v-toolbar-title>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>
                        <v-dialog v-model="dialog" max-width="500px">
                            <template v-slot:activator="{ on }">
                                <v-btn
                                    color="primary"
                                    dark
                                    class="mb-2"
                                    v-on="on"
                                    >New User</v-btn
                                >
                            </template>
                            <v-card>
                                <v-card-title>
                                    <span class="headline">{{
                                        formTitle
                                    }}</span>
                                </v-card-title>

                                <v-card-text>
                                    <v-container>
                                        <v-row>
                                            <v-col cols="12" sm="6" md="4">
                                                <v-text-field
                                                    label="Username"
                                                    v-model="
                                                        editedItem.username
                                                    "
                                                ></v-text-field>
                                            </v-col>
                                            <v-col cols="12" sm="6" md="4">
                                                <v-text-field
                                                    label="Password"
                                                    type="password"
                                                    v-model="
                                                        editedItem.password
                                                    "
                                                ></v-text-field>
                                            </v-col>
                                        </v-row>
                                    </v-container>
                                </v-card-text>

                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="close"
                                        >Cancel</v-btn
                                    >
                                    <v-btn
                                        color="blue darken-1"
                                        text
                                        @click="save"
                                        >Save</v-btn
                                    >
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>
                <template v-slot:item.actions="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)"
                        >mdi-pencil</v-icon
                    >
                </template>
                <template v-slot:item.actions="{ item }">
                    <v-icon small class="mr-2" @click="editItem(item)">
                        mdi-pencil
                    </v-icon>
                    <v-icon small @click="deleteItem(item)">
                        mdi-delete
                    </v-icon>
                </template>
                <template v-slot:no-data> </template>
            </v-data-table>
        </v-card>
    </v-container>
</template>

<script>
export default {
    name: 'ManageUsers',
    data: () => ({
        dialog: false,
        search: '',
        headers: [
            {
                text: 'Username',
                align: 'center',
                sortable: false,
                value: 'username'
            },
            {
                text: 'Actions',
                align: 'center',
                value: 'actions',
                sortable: false
            }
        ],
        users: [],
        editedIndex: -1,
        editedItem: {
            username: '',
            password: ''
        },
        defaultItem: {
            username: '',
            password: ''
        }
    }),

    computed: {
        formTitle() {
            return this.editedIndex === -1 ? 'New Users' : 'Edit Users'
        }
    },

    watch: {
        dialog(val) {
            val || this.close()
        }
    },

    created() {
        this.initialize()
    },

    methods: {
        initialize() {
            this.users = [
                {
                    username: 'admin',
                    password: '12345678'
                }
            ]
        },
        editItem(item) {
            this.editedIndex = this.users.indexOf(item)
            this.editedItem = Object.assign({}, item)
            this.dialog = true
        },

        deleteItem(item) {
            const index = this.users.indexOf(item)
            confirm('Are you sure you want to delete this user?') &&
                this.users.splice(index, 1)
        },

        close() {
            this.dialog = false
            this.$nextTick(() => {
                this.editedItem = Object.assign({}, this.defaultItem)
                this.editedIndex = -1
            })
        },
        save() {
            if (this.editedIndex > -1) {
                Object.assign(this.users[this.editedIndex], this.editedItem)
            } else {
                this.users.push(this.editedItem)
            }
            this.close()
        }
    }
}
</script>

<style lang="scss" scoped></style>
