<template>
  <v-container>
    <v-card class="mx-auto" max-width="344">
      <v-toolbar color="indigo" dark>
        <v-toolbar-title>Login</v-toolbar-title>
      </v-toolbar>
      <v-flex style="padding: 10px 5px 10px 5px">
        <v-form @submit.prevent="login" id="check-login-form">
          <v-text-field v-model="username" label="Username"></v-text-field>
          <v-text-field
            v-model="password"
            label="Password"
            :type="show_pwd ? 'text' : 'password'"
            :append-icon="show_pwd ? 'mdi-eye' : 'mdi-eye-off'"
            @click:append="show_pwd = !show_pwd"
          ></v-text-field>
          <v-btn type="submit" color="success" form="check-login-form"
            >Login</v-btn
          >
        </v-form>
      </v-flex>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: 'Login',
  data: () => ({
    username: '',
    password: '',
    show_pwd: false
  }),
  methods: {
    login() {
      this.$store
        .dispatch('getToken', {
          username: this.username,
          password: this.password
        })
        .then(res => {
          if (res.success) {
            this.$router.push({ name: 'Home' });
            // location.reload()
          }
        });
    }
  }
};
</script>

<style lang="scss" scoped></style>
